@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fraunces:ital,opsz,wght@0,9..144,100..900;1,9..144,100..900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: 0;
}

body {
  background-color: hsl(30, 38%, 92%);
  font-family: "Montserrat", sans-serif;
}

main {
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

p {
  font-size: 14px;
}

h1 {
  font-family: "Fraunces", serif;
  font-size: 30px;
  font-weight: 700;
}

button {
  background-color: hsl(158, 36%, 37%);
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

#price_cont {
  display: flex;
  align-items: center;
  gap: 15px;
}
#price_cont #corte {
  text-decoration: line-through;
}
#price_cont #promo {
  color: hsl(158, 36%, 37%);
  font-family: "Fraunces", serif;
  font-size: 35px;
  font-weight: 700;
}

#container {
  width: 600px;
  height: 450px;
  background: #fff;
  display: flex;
  justify-content: center;
  border-radius: 10px;
}
#container img {
  border-radius: 10px 0px 0px 10px;
}
#container .text_cont {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  flex-direction: column;
}
#container h1 {
  font-size: 35px;
}

#cont_perfume {
  display: flex;
  color: hsl(228, 12%, 48%);
  letter-spacing: 5px;
  font-weight: 400;
}

#title_cont {
  display: flex;
}

#text_cont {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: hsl(228, 12%, 48%);
}